import React, { useEffect } from "react";

export default function FlowWrap({ useCaseNarative, flowName, handleChange, setFlow }) {
    const flow = useCaseNarative[flowName];
    useEffect(() => {
        const length = flow.length;
        if (flow[length - 1].userAction !== "" || flow[length - 1].systemAction !== "") {
            setFlow([...flow, { step: `${length + 1}`, userAction: "", systemAction: "" }]);
        }

        if (
            flow[length - 1].userAction === "" &&
            flow[length - 1].systemAction === "" &&
            length >= 2 &&
            flow[length - 2].userAction === "" &&
            flow[length - 2].systemAction === ""
        ) {
            setFlow([...flow.slice(0, length - 1)]);
        }
    }, [useCaseNarative]);

    const handleTextArea = (e) => {
        handleChange(e);

        const textArea = e.target;
        textArea.style.height = "auto";
        textArea.style.height = textArea.scrollHeight + "px";
    };

    return (
        <section className="flow">
            <h2>
                {flowName === "basicFlow" && "Basic Flow"}
                {flowName === "alternativeFlow" && "Alternative Flow"}
                {flowName === "exceptionFlow" && "Exception Flow"}
            </h2>
            <section className="flowgrid">
                {flowName != "Exception Flow" && (
                    <>
                        <p className="W20 step heading">Step</p>
                        <p className="W40 heading">User Action</p>
                        <p className="W40 heading">System Action</p>
                    </>
                )}
                {flowName === "Exception Flow" && (
                    <>
                        <p className="W20 step heading">Step</p>
                        <p className="W40 heading">Error description</p>
                        <p className="W40 heading">System or User Action</p>
                    </>
                )}
                {flow.map((step, index) => {
                    return (
                        <React.Fragment key={index}>
                            <p className="W10 stepData" key={"s" + index}>
                                <input
                                    type="text"
                                    name={`${flowName}.${index}.step`}
                                    value={step.step}
                                    placeholder="Step"
                                    onChange={handleChange}
                                ></input>
                            </p>
                            <p className="W40" key={"l" + index}>
                                <textarea
                                    type="text"
                                    name={`${flowName}.${index}.userAction`}
                                    value={step.userAction}
                                    placeholder="User Action"
                                    onChange={handleTextArea}
                                ></textarea>
                            </p>
                            <p className="W40" key={"k" + index}>
                                <textarea
                                    type="text"
                                    name={`${flowName}.${index}.systemAction`}
                                    value={step.systemAction}
                                    placeholder="System Action"
                                    onChange={handleTextArea}
                                ></textarea>
                            </p>
                        </React.Fragment>
                    );
                })}
            </section>
        </section>
    );
}
