import { Document, Packer, Paragraph, Table, TableCell, TableRow, WidthType, TextRun, AlignmentType, HeightRule, TableLayoutType } from "docx";
import { saveAs } from "file-saver";

const FULL_WIDTH = 9940;

const generateWordDocument = (dataList) => {
    function decompressData(data) {
        return {
            headingData: {
                caseNr: data.caseNr,
                name: data.name,
                version: data.version,
                endGoal: data.endGoal,
                description: data.description,
                actors: data.actors,
            },
            preConditions: {
                heading: "Preconditions",
                conditions: data.preConditions,
            },
            basicFlow: {
                heading: "Basic Flow",
                heads: ["Step", "User Action", "Server Action"],
                steps: data.basicFlow,
            },
            alternativeFlow: {
                heading: "Alternative Flow",
                heads: ["Step", "User Action", "Server Action"],
                steps: data.alternativeFlow,
            },
            exceptionFlow: {
                heading: "Exception Flow",
                heads: ["Step", "Error description", "System or user actions"],
                steps: data.exceptionFlow,
            },
            postConditions: {
                heading: "Post conditions",
                conditions: data.postConditions,
            },
        };
    }

    const doc = new Document({
        sections: dataList.map((data) => generateSection(decompressData(data))),
    });

    Packer.toBlob(doc).then((blob) => {
        saveAs(blob, "example.docx");
    });
};

const generateSection = (data) => {
    if (data.preConditions.conditions.length === 1 && data.preConditions.conditions[0] === "") {
        data.preConditions.conditions[0] = "...";
    }

    if (data.preConditions.conditions[data.preConditions.conditions.length - 1] === "") {
        data.preConditions.conditions.pop();
    }

    if (data.postConditions.conditions.length === 1 && data.postConditions.conditions[0] === "") {
        data.postConditions.conditions[0] = "...";
    }

    if (data.postConditions.conditions[data.postConditions.conditions.length - 1] === "") {
        data.postConditions.conditions.pop();
    }

    if (
        data.basicFlow.steps[data.basicFlow.steps.length - 1].userAction === "" &&
        data.basicFlow.steps[data.basicFlow.steps.length - 1].systemAction === "" &&
        data.basicFlow.steps.length > 1
    ) {
        data.basicFlow.steps.pop();
    }

    if (
        data.alternativeFlow.steps[data.alternativeFlow.steps.length - 1].userAction === "" &&
        data.alternativeFlow.steps[data.alternativeFlow.steps.length - 1].systemAction === "" &&
        data.alternativeFlow.steps.length > 1
    ) {
        data.alternativeFlow.steps.pop();
    }

    if (
        data.exceptionFlow.steps[data.exceptionFlow.steps.length - 1].userAction === "" &&
        data.exceptionFlow.steps[data.exceptionFlow.steps.length - 1].systemAction === "" &&
        data.exceptionFlow.steps.length > 1
    ) {
        data.exceptionFlow.steps.pop();
    }

    const emptyParagraph = () => {
        return new Paragraph({
            children: [
                new TextRun({
                    text: "",
                }),
            ],
        });
    };
    /**
     * A special function that creates a small table for conditions
     */
    const createConditions = (title, items) => {
        const createTitleRow = (title) => {
            return new TableRow({
                children: [
                    new TableCell({
                        children: [
                            new Paragraph({
                                children: [
                                    new TextRun({
                                        text: title,
                                        font: "Calibri",
                                        bold: true,
                                        color: "ff0000",
                                    }),
                                ],
                                indent: {
                                    left: 80,
                                },
                            }),
                        ],
                        shading: {
                            fill: "#d9d9d9",
                        },
                        width: {
                            size: 9940,
                            type: WidthType.DXA,
                        }
                    }),
                ],
                height: { value: 325, rule: HeightRule.ATLEAST },
            });
        };

        const createListRow = (items) => {
            return new TableRow({
                children: [
                    new TableCell({
                        children: items.map(
                            (item) =>
                                new Paragraph({
                                    bullet: {
                                        level: 0,
                                    },

                                    children: [
                                        new TextRun({
                                            text: item,
                                            font: "Calibri",
                                        }),
                                    ],
                                })
                        ),
                        width: {
                            size: 9940,
                            type: WidthType.DXA,
                        },  
                    }),
                ],
            });
        };

        return new Table({
            columnWidths: [FULL_WIDTH],
            rows: [createTitleRow(title), createListRow(items)],
            indent: {
                size: -460,
                type: WidthType.DXA,
            },
            layout: TableLayoutType.FIXED,
        });
    };

    /**
     * Function that converts data object to table rows
     */
    const createTableRows = (tableData) => {
        return tableData.rows.map((row) => {
            const cells = row.cells.map((cell) => {
                return new TableCell({
                    children: [
                        new Paragraph({
                            children: [
                                new TextRun({
                                    text: cell.text,
                                    bold: cell.bold ? cell.bold : false,
                                    color: cell.color ? cell.color : "#000000",
                                    font: "Calibri",
                                }),
                            ],
                            alignment: cell.alignment ? cell.alignment : AlignmentType.LEFT,
                            indent: {
                                left: 80,
                            },
                        }),
                    ],
                    shading: {
                        fill: cell.background ? cell.background : "#FFFFFF",
                    },
                    columnSpan: cell.columnSpan ? cell.columnSpan : 1,
                });
            });

            return new TableRow({
                children: cells,
                height: { value: 325, rule: HeightRule.ATLEAST },
            });
        });
    };

    /**
     *
     * Add a table wrapper to the table rows
     */
    const createCustomTable = (columnWidths, rows) => {
        return new Table({
            columnWidths: columnWidths,
            indent: {
                size: -460,
                type: WidthType.DXA,
            },
            layout: TableLayoutType.FIXED,
            rows: rows,
        });
    };
    const createHeading = (data) => {
        return {
            rows: [
                {
                    cells: [
                        {
                            text: "Use Case #" + data.caseNr,
                            background: "#d9d9d9",
                            bold: true,
                        },
                        {
                            text: data.name,
                            bold: true,
                        },
                        {
                            text: "Version Nr:",
                            background: "#d9d9d9",
                            bold: true,
                        },
                        {
                            text: data.version,
                        },
                    ],
                },
                {
                    cells: [
                        {
                            text: "End goal:",
                            background: "#d9d9d9",
                            bold: true,
                        },
                        {
                            text: data.endGoal,
                            columnSpan: 3,
                        },
                    ],
                },
                {
                    cells: [
                        {
                            text: "Description:",
                            background: "#d9d9d9",
                            bold: true,
                        },
                        {
                            text: data.description,
                            columnSpan: 3,
                        },
                    ],
                },
                {
                    cells: [
                        {
                            text: "Actors:",
                            background: "#d9d9d9",
                            bold: true,
                        },
                        {
                            text: data.actors,
                            columnSpan: 3,
                        },
                    ],
                },
            ],
        };
    };

    const generateFlow = (data) => {
        const tb = {
            rows: [
                {
                    cells: [
                        {
                            text: data.heading,
                            background: "#d9d9d9",
                            color: "FF0000",
                            bold: true,
                            columnSpan: 3,
                        },
                    ],
                },
                {
                    cells: [
                        {
                            text: data.heads[0],
                            background: "#d9d9d9",
                            bold: true,
                            alignment: AlignmentType.CENTER,
                        },
                        {
                            text: data.heads[1],
                            background: "#d9d9d9",
                            bold: true,
                            alignment: AlignmentType.CENTER,
                        },
                        {
                            text: data.heads[2],
                            background: "#d9d9d9",
                            bold: true,
                            alignment: AlignmentType.CENTER,
                        },
                    ],
                },
            ],
        };

        for (const i in data.steps) {
            tb.rows.push({
                cells: [
                    {
                        text: data.steps[i].step,
                        bold: true,
                    },
                    {
                        text: data.steps[i].userAction,
                    },
                    {
                        text: data.steps[i].systemAction,
                    },
                ],
            });
        }

        return tb;
    };

    return {
        children: [
            createCustomTable([1620, 4320, 1620, 2380], createTableRows(createHeading(data.headingData))),
            emptyParagraph(),
            createConditions(data.preConditions.heading, data.preConditions.conditions),
            emptyParagraph(),
            createCustomTable([1300, 4320, 4320],createTableRows(generateFlow(data.basicFlow))),
            emptyParagraph(),
            createCustomTable([1300, 4320, 4320],createTableRows(generateFlow(data.alternativeFlow))),
            emptyParagraph(),
            createCustomTable([1300, 4320, 4320],createTableRows(generateFlow(data.exceptionFlow))),
            emptyParagraph(),
            createConditions(data.postConditions.heading, data.postConditions.conditions),
            emptyParagraph(),
        ],
    };
};

export default generateWordDocument;
