import React, { useState, useEffect } from "react";

import generateWordDocument from "../components/generateWordDocument";
import { UseCaseNarative } from "../components/useCaseNarative";

export default function Home() {
    const [useCaseNarratives, setUseCaseNarratives] = useState([]);
    const [projects, setProjects] = useState(null);
    
    const [context, setContext] = useState(null);
    const [loading, setLoading] = useState(false);


    useEffect(() => {
        const loadedProjects = JSON.parse(localStorage.getItem('useCaseNarrativesDB')) || { activeProject: 0, projects: [] };
        setProjects(loadedProjects);
        if (loadedProjects.projects.length > 0) {
            setUseCaseNarratives(loadedProjects.projects[loadedProjects.activeProject].narratives || []);
        }
      }, []);


      useEffect(() => {
        if (!projects) return;
        localStorage.setItem('useCaseNarrativesDB', JSON.stringify(projects));
        setUseCaseNarratives(projects.projects[projects.activeProject]?.narratives || []);
      }, [projects]);


      const setNarratives = (narratives) => {
        const newProjects = { ...projects };
        newProjects.projects[newProjects.activeProject].narratives = narratives;
        setProjects(newProjects);
        setUseCaseNarratives(narratives);
      }

    
    const initEmptyUseCaseNarative = () => {
        return {
            caseNr: "",
            name: "",
            version: "",
            endGoal: "",
            description: "",
            actors: "",
            preConditions: [""],
            basicFlow: [
                {
                    step: "1",
                    userAction: "",
                    systemAction: "",
                },
            ],
            alternativeFlow: [
                {
                    step: "1",
                    userAction: "",
                    systemAction: "",
                },
            ],
            exceptionFlow: [
                {
                    step: "1b",
                    userAction: "",
                    systemAction: "",
                },
            ],
            postConditions: [""],
        };
    };

    const jsonSchema = {
        type: "object",
        properties: {
            caseNr: { type: "string" },
            name: { type: "string" },
            version: { type: "string" },
            endGoal: { type: "string" },
            description: { type: "string" },
            actors: { type: "string" },
            preConditions: {
                type: "array",
                items: { type: "string" },
            },
            basicFlow: {
                type: "array",
                items: {
                    type: "object",
                    properties: {
                        step: { type: "string" },
                        userAction: { type: "string" },
                        systemAction: { type: "string" },
                    },
                    required: ["step", "userAction", "systemAction"],
                    additionalProperties: false,
                },
            },
            alternativeFlow: {
                type: "array",
                items: {
                    type: "object",
                    properties: {
                        step: { type: "string" },
                        userAction: { type: "string" },
                        systemAction: { type: "string" },
                    },
                    required: ["step", "userAction", "systemAction"],
                    additionalProperties: false,
                },
            },
            exceptionFlow: {
                type: "array",
                items: {
                    type: "object",
                    properties: {
                        step: { type: "string" },
                        userAction: { type: "string" },
                        systemAction: { type: "string" },
                    },
                    required: ["step", "userAction", "systemAction"],
                    additionalProperties: false,
                },
            },
            postConditions: {
                type: "array",
                items: { type: "string" },
            },
        },
        required: [
            "caseNr",
            "name",
            "version",
            "endGoal",
            "description",
            "actors",
            "preConditions",
            "basicFlow",
            "alternativeFlow",
            "exceptionFlow",
            "postConditions",
        ],
        additionalProperties: false,
    };

    useEffect(() => {
        const handleKeyDown = (e) => {
            if (e.key === "Enter") {
                e.preventDefault();
                const activeElement = document.activeElement;
                const inputs = document.querySelectorAll("input, textarea");
                const inputsArray = Array.from(inputs);
                const index = inputsArray.indexOf(activeElement);

                if (index === inputsArray.length - 1) {
                    inputsArray[0].focus();
                } else {
                    inputsArray[index + 1].focus();
                }
            }
            if (e.key === "Tab") {
                e.preventDefault();

                const activeElement = document.activeElement;
                const inputs = document.querySelectorAll("input, textarea");
                const inputsArray = Array.from(inputs);
                const index = inputsArray.indexOf(activeElement);

                if (index === 0) {
                    inputsArray[inputsArray.length - 1].focus();
                } else {
                    inputsArray[index - 1].focus();
                }
            }
        };

        // Добавляем обработчик события
        document.addEventListener("keydown", handleKeyDown);

        // Убираем обработчик события при размонтировании
        return () => {
            document.removeEventListener("keydown", handleKeyDown);
        };
    }, []);

    return (
        <main>
            <div id="loading" style={{ display: loading ? "block" : "none" }}>
                <div className="loaderWrap">
                    <div className="loader"></div>
                </div>
            </div>

            <div className="tools">
                <section>
                    <h3>Choose Project:</h3>
                    {projects && projects.projects.length > 0 && (
                        <select value={projects.activeProject}
                            onChange={(e) => {
                                setProjects({ ...projects, activeProject: e.target.value });
                            }}
                        >
                            {projects.projects.map((project, index) => (
                                <option value={index} key={"t" + index}>
                                    {project.name}
                                </option>
                            ))}
                        </select>
                    )}
                    <button onClick={() => setProjects({ ...projects, projects: [...projects.projects, { name: `Project ${projects.projects.length + 1}`, narratives: [] }] })}>
                        Add project
                    </button>
                </section>

                <section>
                    <button
                        onClick={() => {
                            const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(projects, null, 2));
                            const downloadAnchorNode = document.createElement("a");
                            downloadAnchorNode.setAttribute("href", dataStr);
                            downloadAnchorNode.setAttribute("download", "useCaseNarratives.json");
                            document.body.appendChild(downloadAnchorNode); // required for firefox
                            downloadAnchorNode.click();
                            downloadAnchorNode.remove();
                        }}
                    >
                        Backup json
                    </button>
                    <h3>Restore from json:</h3>
                    <input
                        type="file"
                        onChange={(e) => {
                            const file = e.target.files[0];
                            const reader = new FileReader();
                            reader.onload = (e) => {
                                const content = e.target.result;
                                try {
                                    const json = JSON.parse(content);
                                    console.log(json);
                                    setProjects(json);
                                    window.alert("Restored successfully, please reload the page manually");
                                } catch (e) {
                                    window.alert("Error: " + e.message);
                                }
                            };
                            reader.readAsText(file);
                        }}
                    />
                </section>

                <button
                    onClick={() => {
                        generateWordDocument(useCaseNarratives);
                    }}
                >
                    Generate Word Document
                </button>

                <button
                    onClick={() => {
                        const accept = window.confirm("Are you sure you want to logout?");
                        if (accept) {
                            localStorage.removeItem("ApiKey");
                            window.location.reload();
                        }
                    }}
                >
                    Logout
                </button>
                <section>
                    <h3>Choose Context:</h3>
                    <input
                        type="file"
                        onChange={(e) => {
                            const file = e.target.files[0];
                            const reader = new FileReader();
                            reader.onload = (e) => {
                                const content = e.target.result;
                                try {
                                    const text = content;
                                    setContext(text);
                                    window.alert("Context file loaded successfully");
                                } catch (e) {
                                    window.alert("Error: " + e.message);
                                }
                            };
                            reader.readAsText(file);
                        }}
                    />
                </section>

            </div>

            <h1>Use Case Narratives by heboba</h1>

            {useCaseNarratives.map((useCaseNarative, index) => {
                const setUseCaseNarative = (useCaseNarative) => {
                    const newUseCaseNarratives = [...useCaseNarratives];
                    newUseCaseNarratives[index] = useCaseNarative;
                    setNarratives(newUseCaseNarratives);
                };
                return (
                    <li key={index}>
                        <div className="quickAcitons">
                            <button
                                className="delete"
                                onClick={() => {
                                    const accept = window.prompt("Are you sure you want to delete this use case? (yes/no)");
                                    if (accept === "yes") {
                                        setNarratives(useCaseNarratives.filter((_, i) => i !== index));
                                    }
                                }}
                            >
                                -
                            </button>
                            <button
                                className="move"
                                onClick={() => {
                                    if (index === 0) return;
                                    const newUseCaseNarratives = [...useCaseNarratives];
                                    const temp = newUseCaseNarratives[index];
                                    newUseCaseNarratives[index] = newUseCaseNarratives[index - 1];
                                    newUseCaseNarratives[index - 1] = temp;
                                    setNarratives(newUseCaseNarratives);
                                }}
                            >
                                ↑
                            </button>

                            <button
                                className="move"
                                onClick={() => {
                                    if (index === useCaseNarratives.length - 1) return;
                                    const newUseCaseNarratives = [...useCaseNarratives];
                                    const temp = newUseCaseNarratives[index];
                                    newUseCaseNarratives[index] = newUseCaseNarratives[index + 1];
                                    newUseCaseNarratives[index + 1] = temp;
                                    setNarratives(newUseCaseNarratives);
                                }}
                            >
                                ↓
                            </button>

                            <button
                                className="askGPT"
                                onClick={() => {
                                    setLoading(true);
                                    fetch("https://api.openai.com/v1/chat/completions", {
                                        method: "POST",
                                        headers: {
                                            "Content-Type": "application/json",
                                            Authorization: `Bearer ${localStorage.getItem("ApiKey")}`,
                                        },
                                        body: JSON.stringify({
                                            model: "gpt-4o-mini",
                                            messages: [
                                                {
                                                    role: "system",
                                                    content:
                                                        "You are a smart assistant that processes requests and responds in JSON format. The user sends you a partially filled JSON data structure, your task is to fill in the remaining fields logically according to the meaning, in the same language in which the other part of the structure is filled. Also, the user's message can include \"context\", this is the text data of the project, from it you can take the essence of the task to better fill in the fields. You can and should add new elements to any arrays if necessary. Your response must be in JSON format only. The main task is to stick to one style. The user will send you 2 JSON structures, one can contain many userStories and it serves as an example for you, what writing style to use. You need to fill in the second structure and return it in the response. THE RESPONSE MUST CONTAIN ONLY THE FILLED SECOND STRUCTURE, YOU ARE NOT ALLOWED TO SEND AN EXAMPLE OR ANY OTHER DATA. Additional description of the structure fields' meaning: caseNr - just the useCase number, for example 1, 2, 3, etc.; name - useCase name, for example 'account aanmaken' or 'melding accenteren' or 'productgegevens wijzigen'; version - 1.0; endGoal - the goal of this action, why the user is doing it.; description - a more detailed description of the actions; actors - persons performing this action, separated by commas; preConditions - what should happen before the action starts.; basicFlow - How the process should ideally proceed step by step.; userAction - always an action performed by the user, for example, pressing a button or entering text; systemAction - how the system reacts to this action, what happens and what the user sees; alternativeFlow - an alternative path, it may start not from step 1, but, for example, from step 3, that is, from the fork step. From the step when the action can be performed differently. It can also be empty in some cases.; exceptionFlow - here userAction means the error description, and systemAction - how the system will react to it; postConditions - result",
                                                },
                                                {
                                                    role: "user",
                                                    content: JSON.stringify({
                                                        context: context,
                                                        exampleStructure: useCaseNarratives,
                                                        structureToFill: useCaseNarative,
                                                    }),
                                                },
                                            ],
                                            response_format: {
                                                type: "json_schema",
                                                json_schema: {
                                                    name: "useCaseNarative",
                                                    schema: jsonSchema,
                                                },
                                            },
                                        }),
                                    })
                                        .then((response) => response.json())
                                        .then((data) => {
                                            const newUseCaseNarative = JSON.parse(data.choices[0].message.content);
                                            setUseCaseNarative(newUseCaseNarative);
                                            setLoading(false);
                                        })
                                        .catch((e) => {
                                            setLoading(false);
                                            window.alert("Error: " + e.message);
                                        });
                                }}
                            >
                                <svg
                                    width="38"
                                    height="38"
                                    viewBox="0 0 41 41"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                    role="img"
                                    className="img"
                                >
                                    <text x="-9999" y="-9999">
                                        ChatGPT
                                    </text>
                                    <path
                                        d="M37.5324 16.8707C37.9808 15.5241 38.1363 14.0974 37.9886 12.6859C37.8409 11.2744 37.3934 9.91076 36.676 8.68622C35.6126 6.83404 33.9882 5.3676 32.0373 4.4985C30.0864 3.62941 27.9098 3.40259 25.8215 3.85078C24.8796 2.7893 23.7219 1.94125 22.4257 1.36341C21.1295 0.785575 19.7249 0.491269 18.3058 0.500197C16.1708 0.495044 14.0893 1.16803 12.3614 2.42214C10.6335 3.67624 9.34853 5.44666 8.6917 7.47815C7.30085 7.76286 5.98686 8.3414 4.8377 9.17505C3.68854 10.0087 2.73073 11.0782 2.02839 12.312C0.956464 14.1591 0.498905 16.2988 0.721698 18.4228C0.944492 20.5467 1.83612 22.5449 3.268 24.1293C2.81966 25.4759 2.66413 26.9026 2.81182 28.3141C2.95951 29.7256 3.40701 31.0892 4.12437 32.3138C5.18791 34.1659 6.8123 35.6322 8.76321 36.5013C10.7141 37.3704 12.8907 37.5973 14.9789 37.1492C15.9208 38.2107 17.0786 39.0587 18.3747 39.6366C19.6709 40.2144 21.0755 40.5087 22.4946 40.4998C24.6307 40.5054 26.7133 39.8321 28.4418 38.5772C30.1704 37.3223 31.4556 35.5506 32.1119 33.5179C33.5027 33.2332 34.8167 32.6547 35.9659 31.821C37.115 30.9874 38.0728 29.9178 38.7752 28.684C39.8458 26.8371 40.3023 24.6979 40.0789 22.5748C39.8556 20.4517 38.9639 18.4544 37.5324 16.8707ZM22.4978 37.8849C20.7443 37.8874 19.0459 37.2733 17.6994 36.1501C17.7601 36.117 17.8666 36.0586 17.936 36.0161L25.9004 31.4156C26.1003 31.3019 26.2663 31.137 26.3813 30.9378C26.4964 30.7386 26.5563 30.5124 26.5549 30.2825V19.0542L29.9213 20.998C29.9389 21.0068 29.9541 21.0198 29.9656 21.0359C29.977 21.052 29.9842 21.0707 29.9867 21.0902V30.3889C29.9842 32.375 29.1946 34.2791 27.7909 35.6841C26.3872 37.0892 24.4838 37.8806 22.4978 37.8849ZM6.39227 31.0064C5.51397 29.4888 5.19742 27.7107 5.49804 25.9832C5.55718 26.0187 5.66048 26.0818 5.73461 26.1244L13.699 30.7248C13.8975 30.8408 14.1233 30.902 14.3532 30.902C14.583 30.902 14.8088 30.8408 15.0073 30.7248L24.731 25.1103V28.9979C24.7321 29.0177 24.7283 29.0376 24.7199 29.0556C24.7115 29.0736 24.6988 29.0893 24.6829 29.1012L16.6317 33.7497C14.9096 34.7416 12.8643 35.0097 10.9447 34.4954C9.02506 33.9811 7.38785 32.7263 6.39227 31.0064ZM4.29707 13.6194C5.17156 12.0998 6.55279 10.9364 8.19885 10.3327C8.19885 10.4013 8.19491 10.5228 8.19491 10.6071V19.808C8.19351 20.0378 8.25334 20.2638 8.36823 20.4629C8.48312 20.6619 8.64893 20.8267 8.84863 20.9404L18.5723 26.5542L15.206 28.4979C15.1894 28.5089 15.1703 28.5155 15.1505 28.5173C15.1307 28.5191 15.1107 28.516 15.0924 28.5082L7.04046 23.8557C5.32135 22.8601 4.06716 21.2235 3.55289 19.3046C3.03862 17.3858 3.30624 15.3413 4.29707 13.6194ZM31.955 20.0556L22.2312 14.4411L25.5976 12.4981C25.6142 12.4872 25.6333 12.4805 25.6531 12.4787C25.6729 12.4769 25.6928 12.4801 25.7111 12.4879L33.7631 17.1364C34.9967 17.849 36.0017 18.8982 36.6606 20.1613C37.3194 21.4244 37.6047 22.849 37.4832 24.2684C37.3617 25.6878 36.8382 27.0432 35.9743 28.1759C35.1103 29.3086 33.9415 30.1717 32.6047 30.6641C32.6047 30.5947 32.6047 30.4733 32.6047 30.3889V21.188C32.6066 20.9586 32.5474 20.7328 32.4332 20.5338C32.319 20.3348 32.154 20.1698 31.955 20.0556ZM35.3055 15.0128C35.2464 14.9765 35.1431 14.9142 35.069 14.8717L27.1045 10.2712C26.906 10.1554 26.6803 10.0943 26.4504 10.0943C26.2206 10.0943 25.9948 10.1554 25.7963 10.2712L16.0726 15.8858V11.9982C16.0715 11.9783 16.0753 11.9585 16.0837 11.9405C16.0921 11.9225 16.1048 11.9068 16.1207 11.8949L24.1719 7.25025C25.4053 6.53903 26.8158 6.19376 28.2383 6.25482C29.6608 6.31589 31.0364 6.78077 32.2044 7.59508C33.3723 8.40939 34.2842 9.53945 34.8334 10.8531C35.3826 12.1667 35.5464 13.6095 35.3055 15.0128ZM14.2424 21.9419L10.8752 19.9981C10.8576 19.9893 10.8423 19.9763 10.8309 19.9602C10.8195 19.9441 10.8122 19.9254 10.8098 19.9058V10.6071C10.8107 9.18295 11.2173 7.78848 11.9819 6.58696C12.7466 5.38544 13.8377 4.42659 15.1275 3.82264C16.4173 3.21869 17.8524 2.99464 19.2649 3.1767C20.6775 3.35876 22.0089 3.93941 23.1034 4.85067C23.0427 4.88379 22.937 4.94215 22.8668 4.98473L14.9024 9.58517C14.7025 9.69878 14.5366 9.86356 14.4215 10.0626C14.3065 10.2616 14.2466 10.4877 14.2479 10.7175L14.2424 21.9419ZM16.071 17.9991L20.4018 15.4978L24.7325 17.9975V22.9985L20.4018 25.4983L16.071 22.9985V17.9991Z"
                                        fill="currentColor"
                                    ></path>
                                </svg>
                            </button>

                            <button
                                className="askGPT"
                                onClick={() => {
                                    setLoading(true);
                                    fetch("https://api.openai.com/v1/chat/completions", {
                                        method: "POST",
                                        headers: {
                                            "Content-Type": "application/json",
                                            Authorization: `Bearer ${localStorage.getItem("ApiKey")}`,
                                        },
                                        body: JSON.stringify({
                                            model: "gpt-4o-mini",
                                            messages: [
                                                {
                                                    role: "system",
                                                    content:
                                                        "You are a smart assistant that processes requests and responds in JSON format. In response, you write only JSON. The user sends you 2 JSON structures, the first one contains all useCase and should be used as an example, from it, you need to take the writing style, the grammar used, the vocabulary, fix misspellings. The second structure is the one you have to work with, everything it contains needs to be translated into Dutch. For a better understanding of the topic and context, you are provided with a \"context\", it contains the entire text of the project, that is, all the necessary information. THE RESPONSE MUST CONTAIN ONLY THE FILLED SECOND STRUCTURE, YOU ARE NOT ALLOWED TO SEND AN EXAMPLE OR ANY OTHER DATA.",
                                                },
                                                {
                                                    role: "user",
                                                    content: JSON.stringify({
                                                        exampleStructure: useCaseNarratives,
                                                        structureToFill: useCaseNarative,
                                                    }),
                                                },
                                            ],
                                            response_format: {
                                                type: "json_schema",
                                                json_schema: {
                                                    name: "useCaseNarative",
                                                    schema: jsonSchema,
                                                },
                                            },
                                        }),
                                    })
                                        .then((response) => response.json())
                                        .then((data) => {
                                            const newUseCaseNarative = JSON.parse(data.choices[0].message.content);
                                            setUseCaseNarative(newUseCaseNarative);
                                            setLoading(false);
                                        })
                                        .catch((e) => {
                                            setLoading(false);
                                            window.alert("Error: " + e.message);
                                        });
                                }}
                            >
                                <img src="/easy-narratives/translate-you-svgrepo-com (1).svg" alt="🇳🇱" className="img" />
                            </button>
                        </div>

                        <UseCaseNarative
                            key={index}
                            useCaseNarative={useCaseNarative}
                            setUseCaseNarative={setUseCaseNarative}
                            useCaseNarratives={useCaseNarratives}
                            index={index + 1}
                        />
                    </li>
                );
            })}

            <button
                className="add"
                onClick={() => {
                    setNarratives([...useCaseNarratives, initEmptyUseCaseNarative()]);
                }}
            >
                +
            </button>
        </main>
    );
}
