import React, { useEffect } from "react";
import FlowWrap from "./flowWrap";

export function UseCaseNarative({ useCaseNarative, setUseCaseNarative, useCaseNaratives, index }) {
    const handleChange = (e) => {
        const { name, value } = e.target;
        const keys = name.split(".");

        const newNarative = { ...useCaseNarative };
        let lastKeyIndex = keys.length - 1;
        let state = newNarative;

        keys.forEach((key, index) => {
            if (index === lastKeyIndex) {
                state[key] = value;
            } else {
                if (!state[key]) state[key] = {};
                state = state[key];
            }
        });

        setUseCaseNarative(newNarative);
    };

    useEffect(() => {
        const length = useCaseNarative.preConditions.length;
        if (useCaseNarative.preConditions[length - 1] !== "") {
            setUseCaseNarative({
                ...useCaseNarative,
                preConditions: [...useCaseNarative.preConditions, ""],
            });
        }

        if (useCaseNarative.preConditions[length - 1] === "" && length >= 2 && useCaseNarative.preConditions[length - 2] === "") {
            setUseCaseNarative({
                ...useCaseNarative,
                preConditions: [...useCaseNarative.preConditions.slice(0, length - 1)],
            });
        }
    }, [useCaseNarative]);

    useEffect(() => {
        const length = useCaseNarative.postConditions.length;
        if (useCaseNarative.postConditions[length - 1] !== "") {
            setUseCaseNarative({
                ...useCaseNarative,
                postConditions: [...useCaseNarative.postConditions, ""],
            });
        }

        if (useCaseNarative.postConditions[length - 1] === "" && length >= 2 && useCaseNarative.postConditions[length - 2] === "") {
            setUseCaseNarative({
                ...useCaseNarative,
                postConditions: [...useCaseNarative.postConditions.slice(0, length - 1)],
            });
        }
    }, [useCaseNarative]);

    return (
        <div className="useCaseNarative">
            <section className="heading">
                <h2>Use Case {index}</h2>
                <div className="row">
                    <p className="W20">
                        <span className="sameWidth">Use Case#</span>
                        <input
                            type="text"
                            name="caseNr"
                            value={useCaseNarative.caseNr}
                            placeholder="Use Case#"
                            onChange={handleChange}
                            maxLength={2}
                        />
                    </p>
                    <p className="W40">
                        <input type="text" name="name" value={useCaseNarative.name} placeholder="Use Case Name" onChange={handleChange}></input>
                    </p>
                    <p className="W40">
                        <span>Use Case Nr:</span>
                        <input type="text" name="version" value={useCaseNarative.version} placeholder="Use Case Nr" onChange={handleChange}></input>
                    </p>
                </div>
                <div className="row">
                    <p>
                        <span className="sameWidth">End Goal:</span>
                        <input type="text" name="endGoal" value={useCaseNarative.endGoal} placeholder="End Goal" onChange={handleChange}></input>
                    </p>
                </div>
                <div className="row">
                    <p>
                        <span className="sameWidth">Description:</span>
                        <input
                            type="text"
                            name="description"
                            value={useCaseNarative.description}
                            placeholder="Description"
                            onChange={handleChange}
                        ></input>
                    </p>
                </div>
                <div className="row">
                    <p>
                        <span className="sameWidth">Actors:</span>
                        <input type="text" name="actors" value={useCaseNarative.actors} placeholder="Actors" onChange={handleChange}></input>
                    </p>
                </div>
            </section>
            <section className="preConditions">
                <h2>Preconditions</h2>
                {useCaseNarative.preConditions.map((condition, index) => {
                    return (
                        <div className="row" key={index}>
                            <p>
                                <input
                                    type="text"
                                    name={`preConditions.${index}`}
                                    value={condition}
                                    placeholder="Preconditions"
                                    onChange={handleChange}
                                ></input>
                            </p>
                        </div>
                    );
                })}
            </section>
            <FlowWrap
                useCaseNarative={useCaseNarative}
                flowName="basicFlow"
                handleChange={handleChange}
                setFlow={(flow) => setUseCaseNarative({ ...useCaseNarative, basicFlow: flow })}
            />
            <FlowWrap
                useCaseNarative={useCaseNarative}
                flowName="alternativeFlow"
                handleChange={handleChange}
                setFlow={(flow) => setUseCaseNarative({ ...useCaseNarative, alternativeFlow: flow })}
            />
            <FlowWrap
                useCaseNarative={useCaseNarative}
                flowName="exceptionFlow"
                handleChange={handleChange}
                setFlow={(flow) => setUseCaseNarative({ ...useCaseNarative, exceptionFlow: flow })}
            />
            <section className="postConditions">
                <h2>Post conditions</h2>
                {useCaseNarative.postConditions.map((condition, index) => {
                    return (
                        <div className="row" key={index}>
                            <p>
                                <input
                                    type="text"
                                    name={`postConditions.${index}`}
                                    value={condition}
                                    placeholder="Post conditions"
                                    onChange={handleChange}
                                ></input>
                            </p>
                        </div>
                    );
                })}
            </section>
        </div>
    );
}
